.proposal-wrapper {
  background: url("../img/proposal/proposal-cat.png") no-repeat center;
  background-size: cover;
  min-height: 290px;

  @media (min-resolution: 2dppx) {
    background: url("../img/proposal/proposal-cat@2x.png") no-repeat center;
    background-size: cover;
  }
}

.proposal-wrapper__background-color {
  box-sizing: border-box;
  padding: 189px 20px 43px 20px;
  min-height: 290px;
  background: rgba(104, 183, 56, 0.85);
}

.proposal-wrapper__text {
  text-align: center;
  margin: 0;
  font-family: $second-font;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: $decorative-color;
}

@media (min-width: 500px) {
  .proposal-wrapper {
    min-height: 500px;
  }

  .proposal-wrapper__background-color {
    min-height: 500px;
  }
}

@media (min-width: $tablet-width) {
  .proposal-wrapper {
    min-height: 200px;
    background: url("../img/proposal/proposal-cat-desktop.png") no-repeat center;
    background-size: cover;

    @media (min-resolution: 2dppx) {
      background: url("../img/proposal/proposal-cat-desktop@2x.png") no-repeat
        center;
      background-size: cover;
    }
  }

  .proposal-wrapper__background-color {
    min-height: 200px;
    padding: 71px 30px 30px 422px;
  }

  .proposal-wrapper__text {
    position: relative;
    font-size: 16px;
    line-height: 20px;
    text-align: left;

    &::before {
      content: "";
      position: absolute;
      top: -14px;
      left: -335px;
      width: 80px;
      height: 83px;
      background: url("../img/proposal/proposal-icon-desktop.svg") no-repeat
        center;
    }
  }
}

@media (min-width: 1000px) {
  .proposal-wrapper {
    min-height: 300px;
  }

  .proposal-wrapper__background-color {
    min-height: 300px;
    padding: 121px 30px 30px 500px;
  }
}

@media (min-width: $desktop-width) {
  .proposal-wrapper {
    min-height: 288px;
    width: 244px;
    background: url("../img/proposal/proposal-cat.png") no-repeat right;

    @media (min-resolution: 2dppx) {
      background: url("../img/proposal/proposal-cat@2x.png") no-repeat center;
      background-size: cover;
    }
  }

  .proposal-wrapper__background-color {
    min-height: 288px;
    width: 244px;
    padding: 185px 25px 25px 25px;
  }

  .proposal-wrapper__text {
    text-align: center;

    &::before {
      top: -120px;
      left: 50px;
    }
  }
}
